* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "montseratt", sans-serif;
  background: url('data:image/jpeg;base64,') no-repeat;
  background-size: 100%;
}

#root {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.app {
  background-size: cover;
  width: 100%;
  padding: 0 10px 0;
}
.app.middle{
  background-image: url("https://images.template.net/wp-content/uploads/2016/06/09114134/Blue-Backgrounds.jpg");
}

.app.cold {
  background-image: url("https://thumbs.dreamstime.com/b/%D1%81%D0%B2%D0%B5%D1%87%D0%B5%D0%BD%D0%B8%D0%B5-%D1%85%D0%BE%D0%BB%D0%BE%D0%B4%D0%BD%D0%BE%D0%B3%D0%BE-%D1%82%D0%B5%D0%BC%D0%BD%D0%BE-%D1%81%D0%B8%D0%BD%D0%B5%D0%B3%D0%BE-%D1%86%D0%B2%D0%B5%D1%82%D0%B0-%D0%B3%D1%80%D0%B0%D0%BD%D1%83%D0%BB%D0%B8%D1%80%D1%83%D0%B5%D1%82-%D0%B0%D0%B1%D1%81%D1%82%D1%80%D0%B0%D0%BA%D1%82%D0%BD%D1%8B%D0%B9-%D1%84%D0%BE%D0%BD-156100394.jpg");
}

.app.warm {
  background-image: url("https://eskipaper.com/images/warm-background-1.jpg");
}

.app.hot {
  background-image: url("https://t4.ftcdn.net/jpg/03/08/66/67/360_F_308666761_bTFYmPovStrp8LlHRBpq2n4tlk61YjE0.jpg");
}

main {
  padding-top: 25px;
  padding-bottom: 50px;
  width: 40%;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: black;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
